.page {
  font-family: Poppins, sans-serif;
  background-color: $secondary-accent-color;
  color: #fff;

  scroll-behavior: smooth;

  caret-color: $secondary-accent-color;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

